.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.box3 {
  box-shadow: 0 0 3px rgba(0, 0, 0, .3);
  position: relative
}

.box3 .box-content, .box3:after, .box3:before {
  position: absolute;
  left: 7%;
  right: 7%;
  transition: all .3s
}

.box3:after, .box3:before {
  display: block;
  background: rgba(0, 0, 0, .3);
  top: 10%;
  bottom: 10%;
  z-index: 1;
  transform: scale(0, 1)
}

.box3:after {
  top: 10.8%;
  bottom: 10.8%;
  transform: scale(1, 0)
}

.box3:hover:after, .box3:hover:before {
  transform: scale(1);
  animation: animate 1.5s
}

.box3:hover:before {
  border-top: 3px solid #fff;
  border-bottom: 3px solid #fff
}

.box:hover:after {
  border-left: 3px solid #fff;
  border-right: 3px solid #fff
}

.box3 img {
  width: 100%;
  height: auto;
  transition: all .3s;
  margin-bottom: 35px
}

.box3:hover img {
  transform: scale(1.2);
  filter: blur(5px);
  -moz-filter: blur(5px);
  -webkit-filter: blur(5px)
}

.box3 .box-content {
  padding: 30px 10px;
  top: 10%;
  bottom: 10%;
  opacity: 0;
  z-index: 2
}

.box3:hover .box-content {
  box-shadow: 0 0 0 35px rgba(255, 255, 255, .3);
  opacity: 1;
  transition: all .3s
}

.box3 .title {
  font-size: 24px;
  font-weight: 600;
  color: #b30000;
  margin: 0 0 5px
}

.box3 .post {
  display: block;
  margin: 0 0 5px;
  font-size: 14px;
  color: rgba(255, 255, 255, .8)
}

.box3 .description {
  font-size: 14px;
  color: #fff;
  margin: 0 0 20px
}

.box3 .icon {
  padding: 0;
  margin: 0;
  list-style: none
}

.box3 .icon li {
  display: inline-block;
  margin: 0 10px 0 0
}

.box3 .icon li a {
  display: block;
  width: 30px;
  height: 30px;
  line-height: 30px;
  color: #fff;
  background: #88c425;
  transition: all .5s
}

.box3 .icon a:hover {
  text-decoration: none;
  animation: animate-hover .5s;
  transition: all .3s
}

@keyframes animate {
  0%, 100% {
    opacity: 1
  }
}

@keyframes animate-hover {
  0% {
    box-shadow: 0 0 0 10px rgba(255, 255, 255, .3)
  }
  50% {
    box-shadow: 0 0 0 5px rgba(255, 255, 255, .3)
  }
  100% {
    box-shadow: 0 0 0 0 rgba(255, 255, 255, .3)
  }
}

.box10, .box11, .box12, .box14, .box4, .box5, .box6, .box7, .box9 {
  box-shadow: 0 0 3px rgba(0, 0, 0, .3)
}

@media only screen and (max-width:990px) {
  .box3 {
    margin-bottom: 30px
  }
  .box3 .box-content {
    padding: 10px
  }
  .box3 .description {
    margin-bottom: 10px
  }
}

@media only screen and (max-width:479px) {
  .box3 .title {
    margin: 0
  }
}